    </div>
    <div class="box">
      <!--  -->
      <van-cell style="margin-bottom: 10px">
        <template #title>
          <van-image
            round
            width="4rem"
            height="4rem"
            :src="userInfo.userFace"
          />
          <span class="name">{{ userInfo.username }}</span>
        </template>
        <template #right-icon>
          <van-button
            plain
            hairline
