          :value="userForm.birth | fmtDate_global"
          @click="editInfo"
        ></van-cell>
        <van-cell
          title=""
          is-link
          class="spanTitle"
          icon="clock-o"
          :value="userForm.registerTime | fmtDate_global"
        ></van-cell>
      </div>
      <!--  -->
      <van-cell
        title=""
        is-link
        class="spanTitle"
        icon="bag-o"
        @click="editPassword"
        style="margin-top: 20px"
      ></van-cell>

      <!--  -->
