<script>
import { get, post, post_formData } from "@/http/axios";
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      userForm: {},
      gender: "",
      show: false,
      passwordShow: false,
      photoShow: false,
      showDatePanel: false,
      // 
      password: "",
      // 
      newPassword: "",
      // 
      fileList: [],
      // 
      timestamp: "",
      minDate: new Date(1920, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(),
      showPickerDate: false,
      bgDate: '', // 
    };
  },
  watch:{
    bgDate: function (val) {
      if (val === '') {
        return
      }
    }, 
  },
  computed: {
    // 
    ...mapState("user", ["userInfo"]),
  },
  methods: {
    // 
    ...mapActions("user", ["getUserInfo"]),
    // 
    changeTimeToChuo() {
      var date = new Date(this.bgDate);
      this.timestamp = date.getTime();
    },
    // 
    formatter (type, value) {
      if (type === 'year') {
        this.value1 = value   // 
        return `${value}`
      } else if (type === 'month') {
        this.value2 = value
        return `${value}`
      }
      this.value3 = value
      return `${value}`
    },
    onCancelDate () {
      this.showPickerDate = false
    },
    onConfirmDate () {
      this.bgDate = `${this.value1}-${this.value2}-${this.value3}`  //  2020-07-03
      this.changeTimeToChuo()
      this.onCancelDate()
    },
    // 
    showBeginDate () {
      this.showPickerDate = true
    },
    //
    //detail  name  index 
    afterRead(file, detail) {
      console.log(this.dataURLtoFileFun(file.content));
      //formData
      let formData = new FormData();
      // base64
      formData.append("file", this.dataURLtoFileFun(file.content, "file.jpg"));
      // post_formDataformDataswagger
      post_formData("http://121.199.29.84:8001/file/upload", formData).then(
        (res) => {
          if (res.data.status === 200) {
            // 
            this.userForm.userFace=
              "http://121.199.29.84:8888/"+
              res.data.data.groupName+
              "/"+
              res.data.data.id;
              console.log(this.userForm.userFace,'-----------');
            // this.userForm.userFace=this.form.userFace
            // 
            this.fileList = [
              {
                url:
                  "http://121.199.29.84:8888/"+
                  res.data.data.groupName+
                  "/"+
                  res.data.data.id,
              },
            ];
          }
        }
      );
    },
    // bae64
    dataURLtoFileFun(dataurl, filename) {
      // base64dataurlbase64filename.jpg,.png
      let arr = dataurl.split(",");
      let mime = arr[0].match(/:(.*?);/)[1];
      let bstr = atob(arr[1]);
      let n = bstr.length;
      let u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    }, 
    // 
    submitPhoto() {
      let data={
        id:this.userForm.id,
        userface:this.userForm.userFace
      }
      post("/baseUser/alterUserface",data).then((res) => {
        if (res.data.status === 200) {
          this.$toast(res.data.message);
          // this.$router.push("mine");
          this.photoShow = false;
          // 
          this.getUserInfo()
        } else {
          this.$toast(res.data.message);
        }
      });
    },
    // 
    submitPassword() {
      if (this.password.length > 3 && this.newPassword.length > 3) {
        this.$dialog
          .confirm({
            title: "",
            message: "",
          })
          .then(async () => {
            if (this.password == this.newPassword) {
              this.userForm.password = this.newPassword;
              let res = await post("/baseUser/saveOrUpdate", this.userForm);
              this.passwordShow = false;
              this.$toast.success(res.data.message);
            } else {
              this.passwordShow = false;
              this.$toast.fail("");
            }
          })
          .catch(() => {});
      } else {
        this.$toast.fail("3");
      }
    },
    // 
    async submitInfo() {
      this.userForm.birth=this.timestamp
      let res = await post("/baseUser/saveOrUpdate", this.userForm);
      this.showGender();
      // 
      this.show = false;
    },

    // 
    showGender() {
      if (this.userForm.gender == "female") {
        this.gender = "";
      }
      if (this.userForm.gender == "male") {
        this.gender = "";
      }
    },
    editInfo() {  
      this.show = true;
    },
    editPassword() {
      this.passwordShow = true;
      this.password = "";
      this.newPassword = "";
    },
    onClickLeft() {
      this.$router.go(-1);
    },

  },
  created() { 
    // this.userForm = { ...this.userInfo };
    // console.log(this.userForm);
    // this.showGender();
  },
};
</script>
